.NavigationBar {
  margin-top: 10px;
  margin-bottom: 30px;
}

.NavigationBarToggler {
  color: white;
}

.NavigationBarBrand {
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 0;
  left: 50%;
  position: absolute;
}

.NavigationLink {
  color: white;
  margin: 5px;
}

.NavigationLink:hover {
  color: white;
  text-decoration: none;
  font-weight: bold;
}



